.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-linkedin-square {
  background: blue;
  color: white;
}

.fa-bandcamp {
  background: green;
  color: white;
}

.fa-github {
  background: #24292e;
  color: white;
}

.fa-youtube {
  background: red;
  color: white;
}

.fa-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.fa-container .fa {
  padding-bottom: 10px;
  width: 100px;
  font-size: 12px;
  border-radius: 90%;

}
.fa {
  padding: 10px;
  font-size: 14px;
  text-align: center;
  text-decoration: none;
  width: 100%;
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
}



