.keys {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.key {
  border: .4rem solid black;
  border-radius: .5rem;
  margin: 1rem;
  font-size: 1.5rem;
  padding: 1rem .5rem;
  width: 10rem;
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  text-shadow: 0 0 .5rem black;
}


.shakeit {
  -webkit-animation: theshakes 0.75s ease;
          animation: theshakes 0.75s ease;
}

@-webkit-keyframes theshakes {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes theshakes {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}


.keydown {
  -webkit-animation: mymove 0.15s ease;
          animation: mymove 0.15s ease;
}

@-webkit-keyframes mymove {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  100% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    border-color: rgb(66, 134, 244);
  }
  100% {
    -webkit-box-shadow: 0 0 1rem rgb(66, 134, 244);
            box-shadow: 0 0 1rem rgb(66, 134, 244);
  }
}

@keyframes mymove {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  100% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    border-color: rgb(66, 134, 244);
  }
  100% {
    -webkit-box-shadow: 0 0 1rem rgb(66, 134, 244);
            box-shadow: 0 0 1rem rgb(66, 134, 244);
  }
}



kbd {
  display: block;
  font-size: 4rem;
}

.sound {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: #ffc600;
}

@media (max-width: 600px) {
  .key {
    width: 5rem;
    font-size: 0.5rem;
  }
  .sound {
    font-size: 0.7rem;
  }
}



